import React from 'react';
import { withStyles, classNames } from '../utils/styles';
import DrawerContent from './DrawerContent';

export const drawerWidth = 240;

const styles = theme => ({
  root: {
    minWidth: drawerWidth,
    maxWidth: drawerWidth,
    borderRightColor: theme.palette.divider,
    borderRightStyle: 'solid',
    borderRightWidth: '1px'
  }
});

class AppDrawer extends React.Component {

  render() {
    const { classes, unlockables, mode, navigateShop, navigatePassword, navigateExercise } = this.props;
    return (
      <div className={classNames(classes.root, classes.hbox)}>
        <DrawerContent unlockables={unlockables} mode={mode} navigateShop={navigateShop} navigatePassword={navigatePassword} navigateExercise={navigateExercise} />
      </div>
    );
  }
}

export default withStyles(styles)(AppDrawer);